$blue-color: #08A6E4;
$background-color: #FFFFFF;
$nav-list-color: #A5A5A5;
$pink-color: #FB3B64;
$green-color: #55DFB4;
$grey-color: #F7F7F7;
$font-raleway: "Raleway", sans-serif;
$font-opensans: "Open Sans", sans-serif;

* {
  margin: 0;
  padding: 0;
}

.header {
  display: flex;
  height: 70px;
  width: 100%;
  background: $background-color;
  box-shadow: 0 3px 6px #00000029;
  justify-content: center;
  position: fixed;
  z-index: 20;

  @media (min-width: 426px) {
    display: flex;
    height: 70px;
    width: 100%;
    background: $background-color;
    box-shadow: 0 3px 6px #00000029;
    justify-content: center;
    position: fixed;
    z-index: 20;
  }
}

.header-title, .footer-title {
  font-family: $font-raleway;
  font-weight: 300;
  font-size: 32px;
  color: $blue-color;
}

.container-header-footer {
  display: flex;
  height: 100%;
  width: 90%;
  /*background: rgba(0, 0, 0, 0.1);*/
  justify-content: space-between;
  align-items: center;
}

.nav {
  display: none;

  @media (min-width: 426px) {
    height: 70px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 70px;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 11px;
  list-style: none;
  margin: 0 30px;
  text-transform: uppercase;
  color: #A5A5A5;
}

.nav-list:hover {
  color: #000000;
  border-bottom: 7px solid #08A6E4;
  cursor: pointer;
}

.main-top {
